.nav__toggle {
  position: relative;
  width: 65px;
  height: 65px;
  background-color: #ffffff;
  padding: 0;
  border: none;
  cursor: pointer;
}

@media (max-width: 767px) {
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    top: 22px;
    left: 22px;
    width: 24px;
    height: 1.5px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }

  .nav--closed .nav__toggle:hover::before {
    opacity: 0.5;
  }

  .nav--closed .nav__toggle:active::before {
    opacity: 0.3;
  }
}
.nav--opened .nav__toggle {
  top: 0;
  left: 0;
}
.nav--opened .nav__toggle::before, .nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  left: 20px;
  width: 26px;
  height: 1.5px;
  background-color: #000000;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
.nav--opened .nav__toggle:hover::before, .nav--opened .nav__toggle:hover::after {
  opacity: 0.5;
}
.nav--opened .nav__toggle:active::before, .nav--opened .nav__toggle:active::after {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .nav--closed .site-list__item {
    display: none;
  }
  .nav--closed .site-list__item--active {
    display: block;
  }
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
