.checkbox__input[type=checkbox]:checked + .checkbox__control-mark::before {
  position: absolute;
  content: "";
  top: 28%;
  left: 24%;
  width: 18px;
  height: 14px;
  background-image: url("../img/svg/stack/stack.svg#checkbox-tick");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.checkbox__input[type=checkbox]:hover ~ .checkbox__control-mark {
  border: 1px solid #bababa;
}

.checkbox__input[type=checkbox]:focus ~ .checkbox__control-mark {
  outline: 2px solid #444444;
}

.checkbox__input[type=checkbox]:disabled ~ .checkbox__control-mark {
  opacity: 0.3;
}

.checkbox__input[type=checkbox]:disabled ~ .checkbox__control-label {
  opacity: 0.3;
}

